"use client";
import { use } from "react";
import VoyageEditForm from "./form";

// On dit clairement que params est une Promise ou un objet
export default function VoyageEditPage({
  params,
}: {
  params: { id: string } | Promise<{ id: string }>;
}) {
  // Résolution garantie
  const resolvedParams: { id: string } = use(params as Promise<{ id: string }> | { id: string });
  const voyageId = resolvedParams.id;

  return <VoyageEditForm voyageId={voyageId} />;
}
