"use client";

import { useEffect, useState, useRef } from "react";
import { useUser } from "@clerk/nextjs";
import { useRouter } from "next/navigation";

export default function VoyageEditForm({ voyageId }: { voyageId: string }) {
  const { user, isLoaded } = useUser();
  const router = useRouter();
  const [voyage, setVoyage] = useState<any | null>(null);
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [error, setError] = useState<string | null>(null);
    type FormValues = {
  lieu: string;
  activite: string;
  date_depart: string;
  date_retour: string;
  classes: string;
  effectif_eleves: number;
  effectif_accompagnateurs: number;
  commentaire: string;
};

const [formValues, setFormValues] = useState<FormValues | null>(null);
  const progInput = useRef<HTMLInputElement | null>(null);
  const filesInput = useRef<HTMLInputElement | null>(null);

  useEffect(() => {
    console.log("useEffect lancé", { isLoaded, user, voyageId });
    if (!isLoaded || !user) return;

    const fetchVoyage = async () => {
      try {
        console.log("fetchVoyage appelé pour id:", voyageId);
        const res = await fetch(`/api/travels/get?voyageId=${voyageId}`);
        console.log("Réponse API:", res.status);
        if (!res.ok) throw new Error("Voyage introuvable");
        const data = await res.json();
        console.log("Voyage reçu:", data);
        setVoyage(data);

        setFormValues({
          lieu: data.lieu,
          activite: data.activite,
          date_depart: data.date_depart,
          date_retour: data.date_retour,
          classes: data.classes,
          effectif_eleves: data.effectif_eleves,
          effectif_accompagnateurs: data.effectif_accompagnateurs,
          commentaire: data.commentaire || "",
        });
      } catch (err: any) {
        console.error("Erreur fetchVoyage:", err);
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchVoyage();
  }, [isLoaded, user, voyageId]);

  if (!isLoaded) return <div>Chargement utilisateur…</div>;
  if (!user) return <div>Veuillez vous connecter.</div>;
  if (loading) return <div>Chargement du voyage…</div>;
  if (error) return <div className="text-red-600">{error}</div>;
  if (!voyage || !formValues) return <div>Voyage introuvable ou formulaire vide.</div>;


const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
  setFormValues((prev: FormValues | null) => {
    if (!prev) return prev;
    return { ...prev, [e.target.name]: e.target.value };
  });
};
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setSaving(true);
    console.log("handleSubmit appelé", formValues);

    try {
      const newProgramme = progInput.current?.files?.[0]
        ? { filename: progInput.current.files[0].name, url: "TODO_upload" }
        : voyage.programme;

      const newPJ = filesInput.current?.files
        ? Array.from(filesInput.current.files).map(f => ({ filename: f.name, url: "TODO_upload" }))
        : voyage.pieces_jointes || [];

      const body = { ...voyage, ...formValues, programme: newProgramme, pieces_jointes: newPJ };
      console.log("Données envoyées à update API:", body);

      const res = await fetch(`/api/travels/update?voyageId=${voyageId}`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(body),
      });

      console.log("Réponse update API:", res.status);
      if (!res.ok) {
        const json = await res.json();
        throw new Error(json.error || "Erreur lors de la mise à jour");
      }

      router.push("/travels");
    } catch (err: any) {
      console.error("Erreur update voyage:", err);
      setError(err.message);
    } finally {
      setSaving(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="pt-[15vh] flex flex-col gap-4 max-w-xl mx-auto">
      <h2 className="text-xl font-bold mb-4">Modifier le voyage</h2>

      <label>
        Lieu / destination :
        <input name="lieu" value={formValues.lieu} onChange={handleChange} required />
      </label>

      <label>
        Activité / motif :
        <input name="activite" value={formValues.activite} onChange={handleChange} required />
      </label>

      <label>
        Date de départ :
        <input type="date" name="date_depart" value={formValues.date_depart} onChange={handleChange} required />
      </label>

      <label>
        Date de retour :
        <input type="date" name="date_retour" value={formValues.date_retour} onChange={handleChange} required />
      </label>

      <label>
        Classes concernées :
        <input name="classes" value={formValues.classes} onChange={handleChange} required />
      </label>

      <label>
        Nombre d’élèves :
        <input type="number" name="effectif_eleves" min={1} value={formValues.effectif_eleves} onChange={handleChange} required />
      </label>

      <label>
        Nombre d’accompagnateurs :
        <input type="number" name="effectif_accompagnateurs" min={1} value={formValues.effectif_accompagnateurs} onChange={handleChange} required />
      </label>

      <label>
        Programme (PDF ou image) :
        <input ref={progInput} type="file" accept=".pdf,.doc,.docx,image/*" />
      </label>

      <label>
        Autres pièces jointes :
        <input ref={filesInput} type="file" multiple accept="image/*,.pdf" />
      </label>

      <label>
        Commentaire :
        <textarea name="commentaire" value={formValues.commentaire} onChange={handleChange} />
      </label>

      <button type="submit" disabled={saving} className="px-4 py-2 bg-green-600 text-white rounded hover:bg-green-700">
        {saving ? "Enregistrement…" : "Mettre à jour"}
      </button>

      {error && <p className="text-red-600 mt-2">{error}</p>}
    </form>
  );
}

